body {
  font-family: Arial, sans-serif;
  line-height: 1.6;
  margin: 0;
  padding: 0;
}

html,
body {
  overflow-x: hidden;
}

.section {
  padding-top: 120px;

  @include mobile {
    padding-top: 60px;
  }

  @include tablet {
    padding-top: 60px;
  }
}

.no-scroll {
  overflow: hidden;
}

button {
  cursor: pointer;
}

.mg-top30 {
  margin-top: 30px;
}

.header {
  padding: 24px 20px;
  text-align: center;

  @include desktop {
    background: #dadfdb22;
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
  }
}

.flex {
  display: flex;

  @include desktop {
    align-items: center;
  }
}

html,
body {
  height: 100%;
  margin: 0;
}

.main {
  flex: 1;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.border {
  // width: 603px;
  width: 100%;
  height: 1px;
  background: $accent-color;
}

.dis-non-mobile {
  @include mobile {
    display: none;
  }

  @include tablet {
    display: none;
  }
}
.dis-non-desktop {
  @include desktop {
    display: none;
  }
}

img {
  width: auto;

  @include mobile {
    max-width: 100%;
  }
}

.gap40 {
  gap: 40px;
}

.mg-top15 {
  margin-top: 15px;
}

.title {
  color: $primary-color;
  font-family: $psytype;
  font-size: 55px;
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0.55px;
  text-transform: uppercase;

  @include mobile {
    font-size: 32px;
    letter-spacing: 0.32px;
  }
}

.title2 {
  color: $primary-color;
  font-family: $psytype;
  font-size: 32px;
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0.32px;
  text-transform: uppercase;
}

.descr {
  color: $primary-color;
  font-family: $manrope;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.subtitle {
  color: $primary-color;
  font-family: $manrope;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
